import React from "react";
import { Spring } from "react-spring/renderprops";

let animate1 = false;
let reversed1 = false;
let animate2 = false;
let reversed2 = false;

const style1 = {
  width: "50%",
  position: "absolute",
  top: "30%",
  marginLeft: "50%",
};

const style2 = {
  width: "75%",
  position: "absolute",
  top: "25%",
};

const movingCloud1 = (source) => {
  return (
    <Spring
      reverse={reversed1}
      onStart={() => {
        animate1 = false;
        reversed1 = !reversed1;
      }}
      reset={animate1}
      onRest={() => (animate1 = true)}
      config={{
        tension: 0.2,
        friction: 0,
        mass: 0.05,
      }}
      from={{ right: "0%" }}
      to={{ right: "-15%" }}
    >
      {(props) => (
        <img
          src={require(`${source}`)}
          alt=""
          style={Object.assign(props, style1)}
        />
      )}
    </Spring>
  );
};

const movingCloud2 = (source) => {
  return (
    <Spring
      reverse={!reversed2}
      onStart={() => {
        animate2 = false;
        reversed2 = !reversed2;
      }}
      reset={animate2}
      onRest={() => (animate2 = true)}
      config={{
        tension: 0.15,
        friction: 0,
        mass: 0.05,
      }}
      from={{ left: "0" }}
      to={{ left: "-10%" }}
    >
      {(props) => (
        <img
          src={require(`${source}`)}
          alt=""
          style={Object.assign(props, style2)}
        />
      )}
    </Spring>
  );
};

const sun = (
  <img
    src={require("./img/sun.png")}
    alt=""
    style={{
      width: "75%",
      height: "75%",
      position: "absolute",
      top: "0%",
      left: "12.5%",
    }}
  />
);

/* const moon = (
  <img
    src={require("./img/moon.png")}
    alt=""
    style={{
      width: "75%",
      height: "75%",
      position: "absolute",
      top: "0",
      left: "12.5%",
    }}
  />
); */

const clouds = (
  <div>
    {movingCloud1("./img/cloud.png")}
    {movingCloud2("./img/cloud.png")}
  </div>
);

const rain = (
  <div>
    {movingCloud1("./img/rain.png")}
    {movingCloud2("./img/rain.png")}
  </div>
);

const thunder = (
  <div>
    {movingCloud1("./img/rain.png")}
    {movingCloud2("./img/thunder.png")}
  </div>
);

const snow = (
  <div>
    {movingCloud1("./img/snow.png")}
    {movingCloud2("./img/snow.png")}
  </div>
);

const lowCloudsSun = (
  <div>
    {sun}
    {movingCloud1("./img/cloud.png")}
    {movingCloud2("./img/cloud.png")}
  </div>
);

/* const lowCloudsMoon = (
  <div>
    {moon}
    {movingCloud1("./img/cloud.png")}
    {movingCloud2("./img/cloud.png")}
  </div>
); */

export default function WeatherAnime(props) {
  const weatherState = () => {
    if (props.state >= 200 && props.state <= 232) {
      return thunder;
    } else if (
      (props.state >= 300 && props.state <= 321) ||
      (props.state >= 500 && props.state <= 531)
    ) {
      return rain;
    } else if (props.state >= 600 && props.state <= 622) {
      return snow;
    } else if (props.state >= 701 && props.state <= 781) {
      return clouds;
    } else if (props.state === 800) {
      return sun;
    } else if (props.state >= 801 && props.state <= 804) {
      return lowCloudsSun;
    }
  };

  return (
    <div
      style={{
        height: 200,
        width: 200,
        margin: "1rem auto",
        position: "relative",
      }}
    >
      {weatherState()}
    </div>
  );
}
