import React from "react";
import { Typography, Card, Container } from "@material-ui/core";
import WbSunnyIcon from "@material-ui/icons/WbSunny";
import NightsStayIcon from "@material-ui/icons/NightsStay";
import CloudIcon from "@material-ui/icons/Cloud";
import { makeStyles } from "@material-ui/core/styles";
import { useSpring, animated } from "react-spring";
//задаем стили
const useStyles = makeStyles({
  footer: {
    display: "flex",
    alignContent: "stretch",
    alignItems: "stretch",
    justifyContent: "space-between",
  },
  simpleCard: {
    flexGrow: 1,
    maxWidth: "33.33%",
  },
  text: { fontFamily: "Comfortaa" },
});

export default function FurtherInfo(props) {
  //переменная стилей
  const classes = useStyles();
  //animation
  const slide1 = useSpring({
    config: { mass: 2 },
    position: "relative",
    left: props.anime ? "0%" : `-100%`,
    maxHeight: props.weather.main !== undefined ? "100%" : "0%",
  });
  // время восхода и захода
  if (props.weather.main !== undefined) {
    const curDate = new Date();

    const formatDate = (time) => {
      return new Date(
        time * 1000 +
          curDate.getTimezoneOffset() * 60000 +
          props.weather.timezone * 1000,
      );
    };

    const formatMins = (mins) => {
      if (mins < 10) {
        return "0" + mins;
      } else {
        return mins;
      }
    };
    const sunriseTime = formatDate(props.weather.sys.sunrise);
    const sunsetTime = formatDate(props.weather.sys.sunset);
    const sunrise = `${sunriseTime.getHours()}:${formatMins(
      sunriseTime.getMinutes(),
    )}`;
    const sunset = `${sunsetTime.getHours()}:${formatMins(
      sunsetTime.getMinutes(),
    )}`;

    return (
      <animated.div style={slide1}>
        <Container className={classes.footer} disableGutters>
          <Card square className={classes.simpleCard}>
            <WbSunnyIcon />
            <Typography className={classes.text}>Восход</Typography>
            <Typography className={classes.text}>{sunrise}</Typography>
          </Card>

          <Card square className={classes.simpleCard}>
            <NightsStayIcon />
            <Typography className={classes.text}>Заход</Typography>
            <Typography className={classes.text}>{sunset}</Typography>
          </Card>

          <Card square className={classes.simpleCard}>
            <CloudIcon />
            <Typography className={classes.text}>Чувствуется</Typography>
            <Typography className={classes.text}>
              {Math.round(props.weather.main.feels_like)}°
            </Typography>
          </Card>
        </Container>
      </animated.div>
    );
  } else {
    return <div></div>;
  }
}
