import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
//задаем стили
const useStyles = makeStyles({
  text: { fontFamily: "Comfortaa" },
});

//задаем дни для вывода
const days = [
  "Воскресенье",
  "Понедельник",
  "Вторник",
  "Среда",
  "Четверг",
  "Пятница",
  "Суббота",
];

//основная функция
export default function MyDate(props) {
  //переменная стилей
  const classes = useStyles();
  //переменные вывода местного времени обьекта поиска
  const curDate = new Date();
  const utc = Date.now() + curDate.getTimezoneOffset() * 60000;
  const day = days[new Date(utc + props.offset * 1000).getDay()];
  const minutes = () => {
    if (new Date(utc + props.offset * 1000).getMinutes() < 10) {
      return "0" + new Date(utc + props.offset * 1000).getMinutes();
    } else {
      return new Date(utc + props.offset * 1000).getMinutes();
    }
  };
  const time = `${new Date(utc + props.offset * 1000).getHours()}:${minutes()}`;

  //вывод местного времени
  return (
    <div>
      <Typography className={classes.text}>
        {day} {time}
      </Typography>
    </div>
  );
}
