import React from "react";
import MyDate from "./MyDate";
import { Typography } from "@material-ui/core";
import WeatherAnime from "./WeatherAnime";
import { makeStyles } from "@material-ui/core/styles";
import { useSpring, animated } from "react-spring";

//задаем стили Material UI
const useStyles = makeStyles({
  text: { fontFamily: "Comfortaa" },
  textBold: {
    fontWeight: 700,
  },
  textShadow: {
    textShadow:
      "-1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);",
  },
});

export default function Weather(props) {
  //переменная стилей
  const classes = useStyles();

  //animation

  const slide1 = useSpring({
    config: { mass: 2 },
    position: "relative",
    left: props.anime ? "0%" : `-100%`,
    maxHeight: props.weather.main !== undefined ? "100%" : "0%",
  });

  const searchAppear = useSpring({
    from: {
      height: "100%",
    },
    to: { height: "0%" },
    reverse: props.weather.main !== undefined,
  });

  return props.weather.main !== undefined ? (
    <animated.div style={slide1}>
      <Typography
        variant="h6"
        className={`${classes.text} ${classes.textBold}`}
      >
        {props.weather.name}
      </Typography>
      <MyDate offset={props.weather.timezone}></MyDate>
      <WeatherAnime state={props.weather.weather[0].id}></WeatherAnime>
      <Typography
        variant="h3"
        className={`${classes.text} ${classes.textBold} ${classes.textShadow}`}
      >
        {Math.round(props.weather.main.temp)}
        °C
      </Typography>
      <Typography className={classes.text}>
        {props.weather.weather[0].description.charAt(0).toUpperCase() +
          props.weather.weather[0].description.slice(1)}
      </Typography>
    </animated.div>
  ) : (
    <animated.div style={searchAppear}></animated.div>
  );
}
