import React, { useState } from "react";
import { Container, Paper } from "@material-ui/core";
import Weather from "./Components/Weather";
import Search from "./Components/Search";
import FurtherInfo from "./Components/FurtherInfo";
import { makeStyles } from "@material-ui/core/styles";

//стили осовной области приложения от Material UI
const useStyles = makeStyles({
  root: {
    minHeight: "-webkit-fill-available",
    height: window.innerHeight,
    display: "flex",
    flexDirection: "column",
    aligntItems: "center",
    justifyContent: "space-between",
    textAlign: "center",
    paddingTop: "1.5rem",
    boxSizing: "border-box",
    overflow: "hidden",
  },
});

//грузим погодный апи
const api = {
  key: "3e24f8ec3ab92f67d68bd5902b5bd088",
  base: "https://api.openweathermap.org/data/2.5/weather?q=",
};

export default function App() {
  const [query, setQuery] = useState("");
  const [weather, setWeather] = useState({});
  const [anime, setAnime] = useState(true);

  //переменная стилей Material UI
  const classes = useStyles();

  //запрос по апи
  const search = () => {
    fetch(`${api.base}${query}&units=metric&lang=ru&appid=${api.key}`)
      .then((res) => res.json())
      .then((result) => {
        setWeather(result);
        setQuery("");
        console.log(result);
        if (result.main !== undefined) {
          setAnime(true);
        }
      });
  };
  //функция анимации погоды

  const animate = () => {
    if (weather.main === undefined) {
      search();
    }
    if (anime === true) {
      setAnime(false);
      setTimeout(() => {
        search();
      }, 500);
    }
  };

  return (
    <Container maxWidth="sm" disableGutters>
      <Paper className={classes.root} elevation={10}>
        <Weather weather={weather} anime={anime}></Weather>
        <Search
          search={animate}
          setQuery={setQuery}
          query={query}
          weather={weather}
        ></Search>
        <FurtherInfo weather={weather} anime={anime}></FurtherInfo>
      </Paper>
    </Container>
  );
}
