import React, { useState } from "react";
import { Spring, config } from "react-spring/renderprops";

export default function WeatherAnime(props) {
  const [animate, setAnimate] = useState(false);
  const [reversed, setReversed] = useState(false);

  const movingCloud1 = (source) => {
    return (
      <Spring
        reverse={reversed}
        onStart={() => {
          setAnimate(false);
          setReversed(!reversed);
        }}
        reset={animate}
        onRest={() => setAnimate(true)}
        config={{
          tension: 1,
          velocity: 0.2,
          friction: 0,
          clamp: true,
          mass: 75,
        }}
        from={{ width: "50%", position: "absolute", top: "30%", right: "-45%" }}
        to={{ width: "50%", position: "absolute", top: "30%", right: "0%" }}
      >
        {(props) => <img src={require(source)} alt="" style={props} />}
      </Spring>
    );
  };

  const movingCloud2 = (source) => {
    return (
      <Spring
        reverse={reversed}
        onStart={() => {
          setAnimate(false);
          setReversed(!reversed);
        }}
        reset={animate}
        onRest={() => setAnimate(true)}
        config={{
          tension: 1,
          velocity: 0.2,
          friction: 0,
          clamp: true,
          mass: 75,
        }}
        from={{ width: "75%", position: "absolute", top: "25%", left: "0%" }}
        to={{ width: "75%", position: "absolute", top: "25%", left: "-50%" }}
      >
        {(props) => <img src={require(source)} alt="" style={props} />}
      </Spring>
    );
  };

  const sun = (
    <img
      src={require("./img/sun.png")}
      alt=""
      style={{
        width: "75%",
        height: "75%",
        position: "absolute",
        top: "12.5%",
        left: "12.5%",
      }}
    />
  );

  const moon = (
    <img
      src={require("./img/moon.png")}
      alt=""
      style={{
        width: "75%",
        height: "75%",
        position: "absolute",
        top: "0",
        left: "12.5%",
      }}
    />
  );

  const clouds = (
    <div>
      <img
        src={require("./img/cloud.png")}
        alt=""
        style={{
          width: "50%",
          position: "absolute",
          top: "30%",
          right: "-15%",
        }}
      />
      <img
        src={require("./img/cloud.png")}
        alt=""
        style={{
          width: "75%",
          position: "absolute",
          top: "25%",
          left: "0%",
        }}
      />
    </div>
  );

  const rain = (
    <div>
      <Spring
        reverse={reversed}
        onStart={() => {
          setAnimate(false);
          setReversed(!reversed);
        }}
        reset={animate}
        onRest={() => setAnimate(true)}
        config={{
          tension: 1,
          velocity: 0.2,
          friction: 0,
          clamp: true,
          mass: 75,
        }}
        from={{ width: "50%", position: "absolute", top: "30%", right: "-45%" }}
        to={{ width: "50%", position: "absolute", top: "30%", right: "0%" }}
      >
        {(props) => (
          <img src={require("./img/rain.png")} alt="" style={props} />
        )}
      </Spring>

      <img
        src={require("./img/rain.png")}
        alt=""
        style={{
          width: "75%",
          position: "absolute",
          top: "25%",
          left: "0%",
        }}
      />
    </div>
  );

  const thunder = (
    <div>
      <img
        src={require("./img/rain.png")}
        alt=""
        style={{
          width: "50%",
          position: "absolute",
          top: "30%",
          right: "-15%",
        }}
      />
      <img
        src={require("./img/thunder.png")}
        alt=""
        style={{
          width: "75%",
          position: "absolute",
          top: "25%",
          left: "0%",
        }}
      />
    </div>
  );

  const snow = (
    <div>
      <img
        src={require("./img/snow.png")}
        alt=""
        style={{
          width: "50%",
          position: "absolute",
          top: "30%",
          right: "-15%",
        }}
      />
      <img
        src={require("./img/snow.png")}
        alt=""
        style={{
          width: "75%",
          position: "absolute",
          top: "25%",
          left: "0%",
        }}
      />
    </div>
  );

  const lowCloudsSun = (
    <div>
      {sun}
      <img
        src={require("./img/cloud.png")}
        alt=""
        style={{
          width: "50%",
          position: "absolute",
          bottom: "10%",
          right: "-5%",
        }}
      />
      <img
        src={require("./img/cloud.png")}
        alt=""
        style={{
          width: "75%",
          position: "absolute",
          bottom: "5%",
          left: "-10%",
        }}
      />
    </div>
  );

  const lowCloudsMoon = (
    <div>
      {moon}
      <img
        src={require("./img/cloud.png")}
        alt=""
        style={{
          width: "50%",
          position: "absolute",
          bottom: "10%",
          right: "-5%",
        }}
      />
      <img
        src={require("./img/cloud.png")}
        alt=""
        style={{
          width: "75%",
          position: "absolute",
          bottom: "5%",
          left: "-10%",
        }}
      />
    </div>
  );

  const weatherState = () => {
    if (props.state >= 200 && props.state <= 232) {
      return thunder;
    } else if (
      (props.state >= 300 && props.state <= 321) ||
      (props.state >= 500 && props.state <= 531)
    ) {
      return rain;
    } else if (props.state >= 600 && props.state <= 622) {
      return snow;
    } else if (props.state >= 701 && props.state <= 781) {
      return clouds;
    } else if (props.state === 800) {
      return sun;
    } else if (props.state >= 801 && props.state <= 804) {
      return lowCloudsSun;
    }
  };

  return (
    <div
      style={{
        height: 200,
        width: 200,
        margin: "2.5rem auto",
        position: "relative",
      }}
    >
      {weatherState()}
    </div>
  );
}
